* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #353535;
  color: #ffffff;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}


main {
  flex-direction: column;
  background-color: #141414d0;
  border-radius: 10px;
  padding: 30px 25px;
  transition: .2s all;
}



